<!--
 * @Autor: linxu
 * @Date: 2021-12-16 15:38:32
 * @LastEditors: linxu
 * @LastEditTime: 2021-12-30 14:41:08
 * @Description: 续约管理表格
-->
<template>
  <div class="renewal-manage">
    <div class="search-box">
      <span class="title">续约管理</span>
      <el-button @click="submitForm('ruleForm')">新增续约</el-button>
    </div>
    <el-table ref="mainTable"
              :data="tableData"
              style="width: 100%"
              height="100%"
              border
              tooltip-effect="light"
              v-loading="loading">
      <!-- 列表无数据 -->
      <template slot="empty">
        <el-empty image="/static/img/nodata.png"
                  :image-size="80"></el-empty>
      </template>
      <!-- <el-table-column
        type="index"
        :index="indexMethod"
        label="序号"
        align="center"
        width="60"
      >
      </el-table-column> -->
      <el-table-column prop="DisplayName"
                       label="续约次序"
                       min-width="8%">
        <template slot-scope="scope">
          <div>{{ scope.row["DisplayName"] }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="Status"
                       label="合同开始时间"
                       min-width="10%">
        <template slot-scope="scope">
          <div>{{ scope.row["Status"] }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="InstallationSite"
                       label="合同结束时间（原始）"
                       min-width="16%">
        <template slot-scope="scope">
          <div>{{ scope.row["InstallationSite"] }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="DisplayName"
                       label="合同结束时间（续约后）"
                       min-width="18%">
        <template slot-scope="scope">
          <div>{{ scope.row["LongitudeAndLatitude"] }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="DisplayName"
                       label="最后修改时间"
                       min-width="12%">
        <template slot-scope="scope">
          <div>{{ scope.row["LongitudeAndLatitude"] }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="DisplayName"
                       label="最后修改人"
                       min-width="9%">
        <template slot-scope="scope">
          <div>{{ scope.row["updator"] }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="DisplayName"
                       label="备注"
                       min-width="8%">
        <template slot-scope="scope">
          <div>{{ scope.row["LongitudeAndLatitude"] }}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作"
                       width="80">
        <template slot-scope="scope">
          <div class="table-hover-show-btn">
            <el-button type="text"
                       @click="handleDelete(scope.row)">删除</el-button>
            <!-- <el-button type="text" @click="downloadFile(scope.row, 1)"
              >续约</el-button
            > -->
          </div>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页器 -->
    <div class="pagination-box mt15">
      <el-pagination align="center"
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="currentPage"
                     :page-sizes="pageSizes"
                     :page-size="pageSize"
                     :layout="layout"
                     :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import { paginationMixin } from '@/mixins/paginationMixin'
export default {
  name: 'renewal-manage',
  mixins: [paginationMixin],
  data () {
    return {
      loading: false
    }
  },
  methods: {
    handleAddRenewal () { },
    handleDelete (row) {

    }
  }
}
</script>
<style lang="less" scoped>
.renewal-manage {
}
</style>



