<!--
 * @Autor: linxu
 * @Date: 2021-11-25 10:10:14
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-18 16:09:40
 * @Description: 合同详情页
-->
<template>
  <div>
    <BMainTopComponent :isShowBackBtn='true' />
    <div class="contract-detail">
      <!-- 合同信息 start -->
      <div class="search-box">
        <span class="title">合同信息</span>
        <el-button @click="submitForm('ruleForm')">编辑</el-button>
      </div>
      <el-form :model="ruleForm"
               :rules="rules"
               ref="ruleForm"
               label-width="120px"
               class="contract-ruleForm">
        <div class="form-items">
          <el-form-item label="签约人:"
                        prop="signatory">
            <el-input v-model="ruleForm.signatory"></el-input>
          </el-form-item>
          <el-form-item label="公司名称:"
                        prop="companyName">
            <el-input v-model="ruleForm.companyName"></el-input>
          </el-form-item>
          <el-form-item label="联系电话:"
                        prop="contactNumber">
            <el-input v-model.number="ruleForm.contactNumber"></el-input>
          </el-form-item>
          <el-form-item label="商务对接:"
                        prop="businessCounterpart">
            <el-input v-model="ruleForm.businessCounterpart"></el-input>
          </el-form-item>
          <el-form-item label="机组名称:"
                        prop="crewName">
            <el-input v-model="ruleForm.crewName"></el-input>
          </el-form-item>
          <el-form-item label="租赁价格:"
                        prop="rentalPrice">
            <el-input v-model="ruleForm.rentalPrice"></el-input>
            <em>元</em>
          </el-form-item>
          <el-form-item label="合同时间:"
                        prop="contractTime">
            <!-- <el-input v-model="ruleForm.contractTime"></el-input> -->
            <el-date-picker v-model="ruleForm.contractTime"
                            type="daterange"
                            value-format="yyyy-MM-dd"
                            range-separator="-"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            placement="bottom-start">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="续约次数:"
                        prop="renewalNum">
            <!-- <el-input v-model="ruleForm.renewalNum"></el-input> -->
            <span>{{ruleForm.renewalNum || 0}}</span>
          </el-form-item>
          <el-form-item label="合同编号:"
                        prop="contractNo">
            <el-input v-model="ruleForm.contractNo"></el-input>
          </el-form-item>
          <el-form-item label="合同状态:"
                        prop="contractStatus">
            <el-select v-model="ruleForm.contractStatus"
                       placeholder="请选择合同状态"
                       :popper-append-to-body="false">
              <el-option label="全部"
                         value=""></el-option>
              <el-option label="生效中"
                         value="1"></el-option>
              <el-option label="即将到期"
                         value="2"></el-option>
              <el-option label="已终止"
                         value="3"></el-option>
            </el-select>
          </el-form-item>
        </div>
      </el-form>
      <!-- 合同信息 end -->

      <!-- 合同文件 start -->
      <contract-file-table></contract-file-table>
      <!-- 合同文件 end -->

      <!-- 续约管理 start -->
      <renewal-manage-table></renewal-manage-table>
      <!-- 续约管理 end -->
    </div>
  </div>
</template>

<script>
import ContractFileTable from './components/ContractFileTable.vue'
import RenewalManageTable from './components/RenewalManageTable.vue'
export default {
  name: 'contract-detail',
  components: {
    ContractFileTable,
    RenewalManageTable
  },
  data () {
    return {
      ruleForm: {
        signatory: '', // 签约人
        companyName: '', // 公司名称
        contactNumber: '', // 联系电话
        businessCounterpart: '', // 商务对接
        crewName: '', // 机组名称
        rentalPrice: '', // 租赁价格
        contractTime: '', // 合同时间
        renewalNum: '', // 续约次数
        contractNo: '', // 合同编号
        contractStatus: '1' // 合同状态
      },
      rules: {
        signatory: [
          { required: true, message: '请输入签约人名称', trigger: 'blur' }
        ],
        companyName: [
          { required: true, message: '请输入公司名称', trigger: 'blur' }
        ],
        contactNumber: [
          { required: true, message: '请输入联系电话', trigger: 'blur' }
        ],
        businessCounterpart: [
          { required: true, message: '请选择商务对接人', trigger: 'change' }
        ],
        crewName: [
          { required: true, message: '请选择机组', trigger: 'change' }
        ],
        rentalPrice: [
          { required: true, message: '请输入租赁价格', trigger: 'blur' }
        ],
        contractTime: [
          { required: true, message: '请选择合同起止时间', trigger: 'change' }
        ],
        contractNo: [
          { required: true, message: '请输入合同编号', trigger: 'change' }
        ],
        contractStatus: [
          { required: true, message: '请选择合同状态', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 提交合同信息表单
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.ruleForm)
        } else {
          console.log('error submit!!')
          this.$message.warning('请先填写表单必填项')
          return false
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.contract-detail {
  .contract-ruleForm {
    .form-items {
      display: flex;
      flex-wrap: wrap;
      .el-form-item {
        width: 33.3%;
        /deep/.el-form-item__content {
          display: flex;
          > em {
            width: 50px;
            text-align: center;
          }
        }
        /deep/.el-date-editor--daterange.el-input__inner {
          width: 100%;
        }
      }
    }
  }
}
</style>



