export const paginationMixin = {
  data () {
    return {
      tableData: [],
      total: 0,
      currentPage: 1,
      pageSize: 10,
      loading: false,
      layout: 'total, sizes, prev, pager, next, jumper',
      pageSizes: [10, 20, 30, 40]
    }
  },
  created () {},
  mounted () {},
  methods: {
    // 序号Index 翻页递增  currentPage 当前页, pageSize 当前页显示条数
    indexMethod (index) {
      return (this.currentPage - 1) * this.pageSize + index + 1
    },
    // 每页条数改变时触发 选择一页显示多少行
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
      this.currentPage = 1
      this.pageSize = val
      this.queryTableData()
    },
    // 当前页改变时触发 跳转其他页
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
      this.queryTableData()
    },
    resetCurrentParams () {
      this.currentPage = 1
      this.pageSize = 10
    }
  },
  watch: {}
}



