<!--
 * @Autor: linxu
 * @Date: 2021-12-16 15:23:53
 * @LastEditors: linxu
 * @LastEditTime: 2022-02-14 17:02:21
 * @Description: 合同文件表格
-->
<template>
  <div class="contract-file">
    <div class="search-box">
      <span class="title">合同文件</span>
      <el-button @click="handleUploadFile()">上传文件</el-button>
    </div>
    <el-table ref="mainTable"
              :data="tableData"
              style="width: 100%"
              height="100%"
              border
              tooltip-effect="light"
              v-loading="loading">
      <!-- 列表无数据 -->
      <!-- <template slot="empty">
        <el-empty image="/static/img/nodata.png"
                  :image-size="80"></el-empty>
      </template> -->
      <el-table-column type="index"
                       :index="indexMethod"
                       label="序号"
                       align="center"
                       width="60">
      </el-table-column>
      <el-table-column prop="DisplayName"
                       label="关联合同"
                       min-width="12%">
        <template slot-scope="scope">
          <div>{{ scope.row["DisplayName"] }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="Status"
                       label="文件类型"
                       min-width="6%">
        <template slot-scope="scope">
          <div>{{ scope.row["Status"] }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="InstallationSite"
                       label="文件名称"
                       min-width="12%">
        <template slot-scope="scope">
          <div>{{ scope.row["InstallationSite"] }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="DisplayName"
                       label="上传时间"
                       min-width="8%">
        <template slot-scope="scope">
          <div>{{ scope.row["LongitudeAndLatitude"] }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="DisplayName"
                       label="上传人"
                       min-width="8%">
        <template slot-scope="scope">
          <div>{{ scope.row["LongitudeAndLatitude"] }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="DisplayName"
                       label="备注"
                       min-width="8%">
        <template slot-scope="scope">
          <div>{{ scope.row["LongitudeAndLatitude"] }}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作"
                       width="80">
        <template slot-scope="scope">
          <div class="table-hover-show-btn">
            <el-button type="text"
                       @click="handleDownloadFile(scope.row)">下载文件</el-button>
            <!-- <el-button type="text" @click="downloadFile(scope.row, 1)"
              >续约</el-button
            > -->
          </div>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页器 -->
    <div class="pagination-box mt15">
      <el-pagination align="center"
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="currentPage"
                     :page-sizes="pageSizes"
                     :page-size="pageSize"
                     :layout="layout"
                     :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import { paginationMixin } from '@/mixins/paginationMixin'
export default {
  name: 'contract-file',
  mixins: [paginationMixin],
  data () {
    return {
      loading: false,
      tableData: []
    }
  },
  methods: {
    handleUploadFile () { },
    handleDownloadFile (row) {

    }
  }
}
</script>
<style lang="less" scoped>
.contract-file {
  margin-bottom: 16px;
}
</style>



